import React from 'react';
import { Link } from 'react-router-dom';

const NavBar = () => {
    return(
        <nav>
             <ul>
                <li>
                    <Link to="/">Home Page</Link>
                </li>
                <li>
                    <Link to="/aboutus">About us</Link>
                </li>
                <li>
                    <Link to="/projectList">project List</Link>
                </li>
            </ul>   
        </nav>
      
    );
}

export default NavBar;