import React, {useState,useEffect}from 'react';
import projectContent from './ProjectContent';
import ProjectList from '../components/ProjectList';
import NotFound from './NotFound';
import CommentsList from '../components/CommentsList';
import UpvoteSection from '../components/UpvoteSection';
import AddCommentForm from '../components/AddCommentForm';

const Project = ({match}) => {
    const name = match.params.name;
    const project = projectContent.find( project => project.name === name);
    const [ projectInfo, setProjectInfo ] = useState({upvote : 0, comments : [] }  );

    useEffect (() => {
          const fetchData = async () => {
                const result = await fetch(`/api/projects/${name}`);
                const body =  await result.json();
                console.log(body);
                setProjectInfo(body);
          }
          fetchData();
    }, [name]);

    if( !project){
        return (
            <>
            <p></p>
            <NotFound/>
            </>
            );
    }
    const otherArticles = projectContent.filter( project => project.name !== name)
    return(
        <>
         <br></br>
         <br></br>
        <h1> {project.title} </h1>
       <UpvoteSection projectName={name} upvote={projectInfo.upvote} setProjectInfo={setProjectInfo} ></UpvoteSection>
        
        { project.content.map((paragraph, key) => (
            <p key={key}>{paragraph}</p>
        ))}
        <CommentsList comments={ projectInfo.comments}></CommentsList>
        <AddCommentForm setProjectInfo={setProjectInfo} projectName={name}></AddCommentForm>
        <h3>Other Projects</h3>
        <ProjectList projects={otherArticles}/>
        </>
    );
}

export default Project;